import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "About",
    component: () => import("@/views/desktop.vue")
  },
  {
    path: "/test",
    name: "test",
    component: () => import("@/views/test.vue")
  }
];

const router = new VueRouter({
  routes
});

export default router;
